import React from 'react';
import './App.scss';

class App extends React.Component {
  state = {
    count: 0,
  }

  addOne = () => {
    this.setState(state => ({
      count: state.count + 1,
    }));
  };

  add100 = () => {
    this.setState(state => ({
      count: state.count + 100,
    }));
  };

  ifDivisible = () => {
    this.addOne();
    this.add100();
  }

  increase = () => {
    this.setState(state => (
      state.count % 5 === 0
        ? this.ifDivisible()
        : this.addOne()
    ));
  };

  render() {
    const { count } = this.state;

    return (
      <>
        <h1 className="title">
          Count:
          {' '}
          <span className="count">{count}</span>
        </h1>
        <button
          type="button"
          className="button"
          onClick={this.addOne}
        >
          Add 1
        </button>
        <button
          type="button"
          className="button"
          onClick={this.add100}
        >
          Add 100
        </button>
        <button
          type="button"
          className="button"
          onClick={this.increase}
        >
          Increase
        </button>
      </>
    );
  }
}

export default App;
